



































































import { Component, Vue, Watch } from 'vue-property-decorator';
import formDisplay from './../compoents/formDisplay.vue';
import { dataViewFields } from '@api/viewData';
import { arrayFind } from '@utils/util';
import pinyin4js from 'pinyin4js';
import RightSet from './rightSet.vue';
import cloneDeep from 'lodash/cloneDeep';
import { namespace } from 'vuex-class';
const processTemplate = namespace('processTemplate');
// import DialogSet from './dialogSet.vue';

interface FormAttribute {
  labelPosition: string;
  labelSuffix: string;
  labelWidth: string;
  formList: Array<any>;
}
interface ExtendSet {
  extendColName: string;
  addDataSetVal: string;
  addDataQueryList: Array<any>;
  addDataValue: Array<any>;
  addSetType: string;
  tabList: Array<any>;
  addViewDataLists: Array<any>;
  colsAdd: Array<any>;
}
interface DefaultValueBind {
  cfgType: string;
  bindType: string;
  bindTarget: string;
  bindField: string;
  bindExpress: any;
  targetType: string;
  valueShow: string;
  parameterList: Array<any>;
}

interface ShowConfig {
  showType: string;
  valueShow: string;
  targetType: string;
  bindField: string;
  symbolShow: string;
  inputValue: string;
  parameterList: Array<any>;
  relationship: string;
}

interface ItemAttribute {
  sourceDataType: string;
  type: string;
  label: string;
  key: string;
  placeHolder: string;
  value: string;
  span: number;
  itemMultiple: boolean;
  itemMultipleLimit: number;
  clearAble: boolean;
  disabled: boolean;
  required: boolean;
  cols: Array<any>;
  dataLabel: string;
  dataValue: string;
  dataSetVal: string;
  dataQueryList: Array<any>;
  editable: boolean;
  readable: boolean;
  recordable: boolean;
  options: Array<any>;
  rules: Array<any>;
  associatedNodeList: Array<any>;
  bindValue: DateSetFrom;
  addSetList: Array<ExtendSet>;
  flowVisibleCode: string;
  addLine: boolean;
  performResult: string;
  parameterList: Array<any>;
  dictFlagValue: number;
  defaultValue: DefaultValueBind | null;
  showConfig: ShowConfig | null;
  remoteDataUrl: string;
  remoteDataLabel: string;
  remoteDataValue: string;
  remoteDataType: string;
  centerFont: boolean;
  description: string;
  alertType: string;
  buttonType: string;
  showIcon: boolean;
  mutexSetList: Array<ExtendSet>;
  origId: string;
  filterText: Array<FilterText>;
  filterConfig: FilterConfig | null;
  refreshLoading: false;
  loadingPrimKey: Array<string>;
  splitTable: Array<string>;
  addMore: false;
}
interface FilterText {
  filterType: string;
  textLinkMethod: string;
  textLink: string;
  textLinkValue: string;
}
interface FilterConfig {
  cfgType: string;
  valueShow: string;
  parameterList: Array<any>;
  filterText: Array<FilterText>;
}
interface DateSetFrom {
  dateValue: string;
  dateSpecify: string;
  systemDate: string;
  variableDate: string;
  selectDate: string;
  countDate: string;
  numDate: number;
  predestination: string;
}
@Component({
  components: {
    formDisplay,
    // DialogSet,
    RightSet
  }
})
export default class CustomForm extends Vue {
  @processTemplate.State((state) => state.customForm) globalFormAttribute;
  private iconNav: Array<any> = [
    {
      title: '输入字段',
      children: [
        {
          type: 'text',
          title: '单行文本',
          iconClass: 'form-tool-text'
        },
        {
          type: 'number',
          title: '数字',
          iconClass: 'form-tool-inputNumber'
        },
        {
          type: 'textarea',
          title: '多行文本',
          iconClass: 'form-tool-textarea'
        }
      ]
    },
    {
      title: '选择字段',
      children: [
        {
          type: 'date',
          title: '日期',
          iconClass: 'form-tool-date'
        },
        {
          type: 'select',
          title: '下拉选择器',
          iconClass: 'form-tool-select'
        },
        {
          type: 'radioGroup',
          title: '单选框组',
          iconClass: 'form-tool-radioGroup'
        },
        {
          type: 'checkboxGroup',
          title: '多选框组',
          iconClass: 'form-tool-checkboxGroup'
        },
        {
          type: 'transfer',
          title: '穿梭框',
          iconClass: 'form-tool-checkboxGroup'
        }
      ]
    },
    {
      title: '复杂字段',
      children: [
        {
          type: 'table',
          title: '表格',
          iconClass: 'form-tool-table'
        },
        {
          type: 'ueditor',
          title: '富文本',
          iconClass: 'form-tool-ueditor'
        }
      ]
    },
    {
      title: '其他字段',
      children: [
        {
          type: 'switch',
          title: '开关',
          iconClass: 'form-tool-switch'
        },
        {
          type: 'upload',
          title: '上传',
          iconClass: 'folder-upload-line'
        },
        {
          type: 'alert',
          title: '提示卡',
          iconClass: 'folder-alert-line'
        },
        {
          type: 'hidden',
          title: '隐藏项',
          iconClass: 'hidden-icon'
        },
        {
          type: 'button',
          title: '按钮',
          iconClass: 'button-icon'
        },
        {
          type: 'password',
          title: '密码框',
          iconClass: 'hidden-icon'
        },
        {
          type: 'childFrom',
          title: '子表单',
          iconClass: 'from-icon'
        }
      ]
    }
  ];
  private draggingType = ''; // 当前拖拽的组件的类型
  private draggingArea = '';
  // private sourceDataType = 'static';
  private draggingAreaType = '';
  private dataSet: Array<any> = [];
  private viewDataSet: Array<any> = [];
  private optionLabel: Array<any> = [];
  private itemAttributeFather: any = {};
  private formAttributeFather: any = {};
  private childFromBoolean = false;
  private childArr: Array<any> = [];
  private formAttribute: FormAttribute = {
    labelPosition: 'right',
    labelSuffix: '：',
    labelWidth: '120',
    formList: []
  };
  private itemAttribute: ItemAttribute = {
    sourceDataType: '',
    type: '',
    label: '',
    key: '',
    placeHolder: '',
    value: '',
    span: 24,
    itemMultiple: false,
    itemMultipleLimit: 1,
    clearAble: false,
    disabled: false,
    required: false,
    options: [],
    parameterList: [],
    performResult: '',
    cols: [],
    dataLabel: '',
    dataValue: '',
    dataSetVal: '',
    dataQueryList: [],
    associatedNodeList: [],
    alertType: '',
    buttonType: '',
    centerFont: false,
    description: '',
    showIcon: false,
    editable: false,
    readable: false,
    recordable: false,
    rules: [
      {
        required: false,
        message: ''
      }
    ],
    bindValue: {
      dateValue: '',
      dateSpecify: '',
      systemDate: '',
      variableDate: '',
      selectDate: '',
      countDate: '',
      numDate: 0,
      predestination: ''
    },
    addLine: false,
    addSetList: [],
    flowVisibleCode: '',
    dictFlagValue: 1,
    defaultValue: null,
    remoteDataUrl: '',
    remoteDataLabel: '',
    remoteDataValue: '',
    showConfig: null,
    remoteDataType: 'get',
    mutexSetList: [],
    origId: '',
    filterText: [],
    filterConfig: null,
    refreshLoading: false,
    loadingPrimKey: [],
    splitTable: [],
    addMore: false
  };

  @Watch('globalFormAttribute')
  watchForm(newVal) {
    if (newVal && newVal.formList) {
      this.queryFormData();
    }
  }
  handleDragStart(e, type) {
    this.draggingType = type;
    this.draggingArea = 'draggingTooles';
  }
  handleSomethingDrop() {
    if (this.draggingArea === 'draggingTooles') {
      this.generateFormItem('single', 24);
    }
  }
  // 子表单取消
  childCancle() {
    this.formAttribute = this.formAttributeFather;
    this.itemAttribute = this.itemAttributeFather;
  }
  // 子表单保存
  childSave(formAttribute, childKey) {
    this.formAttributeFather.formList.forEach((item) => {
      if (item.type === 'childFrom' && item.key === childKey) {
        item.value = formAttribute;
      } else if (item.type === 'table' && item.key === childKey) {
        item.addTemplate = formAttribute;
      }
    });
    console.log(this.formAttributeFather);
    this.itemAttributeFather.value = formAttribute;
    this.formAttribute = this.formAttributeFather;
    this.itemAttribute = this.itemAttributeFather;
  }
  // 子表单显示
  fromChange(itemAtt) {
    this.formAttributeFather = this.formAttribute;
    this.itemAttributeFather = this.itemAttribute;
    this.childFromBoolean = true;
    if (itemAtt.value.formList) {
      this.formAttribute = itemAtt.value;
    } else {
      if (this.childFromBoolean === true) {
        this.formAttribute = {
          labelPosition: 'right',
          labelSuffix: '：',
          labelWidth: '120',
          formList: []
        };
        this.itemAttribute = {
          sourceDataType: '',
          type: '',
          label: '',
          key: '',
          placeHolder: '',
          value: '',
          span: 24,
          itemMultiple: false,
          itemMultipleLimit: 1,
          clearAble: false,
          disabled: false,
          required: false,
          options: [],
          parameterList: [],
          performResult: '',
          cols: [],
          dataLabel: '',
          dataValue: '',
          dataSetVal: '',
          dataQueryList: [],
          associatedNodeList: [],
          alertType: '',
          buttonType: '',
          centerFont: false,
          description: '',
          showIcon: false,
          editable: false,
          readable: false,
          recordable: false,
          rules: [
            {
              required: false,
              message: ''
            }
          ],
          bindValue: {
            dateValue: '',
            dateSpecify: '',
            systemDate: '',
            variableDate: '',
            selectDate: '',
            countDate: '',
            numDate: 0,
            predestination: ''
          },
          addLine: false,
          addSetList: [],
          flowVisibleCode: '',
          dictFlagValue: 1,
          defaultValue: null,
          remoteDataUrl: '',
          remoteDataLabel: '',
          remoteDataValue: '',
          showConfig: null,
          remoteDataType: 'get',
          mutexSetList: [],
          origId: '',
          filterText: [],
          filterConfig: null,
          refreshLoading: false,
          loadingPrimKey: [],
          splitTable: [],
          addMore: false
        };
      }
    }
  }
  // 拖拽表单项数据处理
  generateFormItem(type, val) {
    let formItem: any = {};
    const keyRandom = 'yejijizhunchae';
    formItem = {
      sourceDataType: 'static',
      type: this.draggingType,
      key: keyRandom,
      placeHolder: '请输入',
      value: '',
      span: val,
      editable: false,
      readable: false,
      recordable: false,
      clearAble: false,
      disabled: false,
      required: false,
      rules: [
        {
          required: false,
          message: ''
        }
      ],
      dataQueryList: [],
      performResult: '',
      parameterList: [],
      substanceText: '',
      addSetList: [],
      mutexSetList: [],
      origId: '',
      bindValue: {},
      addLine: false,
      remoteDataUrl: '',
      remoteDataLabel: '',
      remoteDataValue: '',
      remoteDataType: 'get',
      filterText: [],
      addTemplate: {},
      splitTable: [],
      addMore: false
    };
    if (this.draggingType === 'text') {
      formItem.label = '单行文本';
      formItem.sourceDataType = 'dataView';
    } else if (this.draggingType === 'number') {
      formItem.label = '数字';
      formItem.placeHolder = '请输入数字';
      formItem.sourceDataType = 'dataView';
      formItem.accuracy = 2;
    } else if (this.draggingType === 'textarea') {
      formItem.label = '多行文本';
      formItem.sourceDataType = 'dataView';
    } else if (this.draggingType === 'table') {
      formItem.label = '表格';
      formItem.cols = [];
      formItem.value = [];
      formItem.datas = [];
      formItem.associatedNodeList = [];
      formItem.sourceDataType = 'dataView';
      formItem.dictFlagValue = 1;
      formItem.refreshLoading = false;
      formItem.loadingPrimKey = [];
      formItem.addTemplate = {};
      formItem.splitTable = [];
    } else if (this.draggingType === 'date') {
      formItem.label = '日期';
      formItem.bindValue = {
        dateValue: '',
        dateSpecify: '',
        systemDate: '',
        variableDate: '',
        selectDate: '',
        countDate: '',
        numDate: 0,
        predestination: ''
      };
      formItem.sourceDataType = 'dataView';
    } else if (this.draggingType === 'select') {
      formItem.label = '下拉选择器';
      formItem.value = [];
      formItem.options = [];
      formItem.itemMultiple = false;
      formItem.itemMultipleLimit = 1;
      formItem.associatedNodeList = [];
    } else if (this.draggingType === 'cascader') {
      formItem.label = '级联选择器';
      formItem.options = [];
      formItem.value = [];
      formItem.associatedNodeList = [];
    } else if (this.draggingType === 'radioGroup') {
      formItem.label = '单选框组';
      formItem.associatedNodeList = [];
      formItem.options = [];
    } else if (this.draggingType === 'checkboxGroup') {
      formItem.label = '多选框组';
      formItem.options = [];
      formItem.value = [];
      formItem.associatedNodeList = [];
    } else if (this.draggingType === 'inputNumber') {
      formItem.label = '计数器';
      formItem.bindValue = {
        countDate: '',
        numDate: ''
      };
    } else if (this.draggingType === 'switch') {
      formItem.label = '开关';
      formItem.value = false;
    } else if (this.draggingType === 'ueditor') {
      formItem.label = '富文本编辑器';
    } else if (this.draggingType === 'upload') {
      formItem.label = '上传';
      formItem.value = [];
    } else if (this.draggingType === 'alert') {
      formItem.label = '提示卡';
      formItem.value = '';
      formItem.alertType = 'success';
      formItem.description = '';
      formItem.centerFont = false;
      formItem.showIcon = true;
    } else if (this.draggingType === 'hidden') {
      formItem.label = '隐藏项';
      formItem.value = '';
    } else if (this.draggingType === 'button') {
      formItem.label = '按钮';
      formItem.buttonType = '';
      formItem.value = '按钮';
    } else if (this.draggingType === 'childFrom') {
      formItem.label = '子表单';
      formItem.value = {};
    } else if (this.draggingType === 'password') {
      formItem.label = '密码框';
      formItem.value = '';
    } else if (this.draggingType === 'transfer') {
      formItem.label = '穿梭框';
      formItem.value = [];
      formItem.options = [];
      formItem.itemMultiple = false;
      formItem.itemMultipleLimit = 1;
      formItem.associatedNodeList = [];
    }

    if (type === 'single') {
      this.formAttribute.formList.push(formItem);
    }
    if (formItem.type === 'button') {
      this.getDataPushSet();
    }
    this.draggingType = '';
    if (formItem.key && formItem.key !== '') {
      this.itemAttribute = formItem;
    }
    const AttributeList: Array<any> = [];
    this.formAttribute.formList.forEach((item) => {
      if (item.key === formItem.key) {
        AttributeList.push(item);
      }
      if (AttributeList.length > 1) {
        const sa = Math.floor(Math.random() * 90) + 10;
        formItem.key = pinyin4js
          .convertToPinyinString(formItem.label, '', pinyin4js.WITHOUT_TONE)
          .concat(sa.toString());
      } else {
        formItem.key = pinyin4js.convertToPinyinString(
          formItem.label,
          '',
          pinyin4js.WITHOUT_TONE
        );
      }
    });
  }
  draggingAreaChange(msg) {
    this.draggingArea = msg;
  }
  draggingAreaTypeChange(msg) {
    this.draggingAreaType = msg;
  }
  // 鼠标点击当前表单项
  clickActiveItem(item: any) {
    this.childArr = [];
    this.optionLabel = arrayFind(this.dataSet, 'id', item.dataSetVal)
      ? arrayFind(this.dataSet, 'id', item.dataSetVal)['fields']
      : [];
    if (this.optionLabel.length > 0) {
      const theSet = cloneDeep(this.optionLabel);
      if (item.type === 'select') {
        item.cols = theSet.map((col) => {
          return {
            ...col,
            prop: col.fieldCode,
            label: col.fieldName
          };
        });
      }
    }
    if (
      item.dataLabel &&
      !Array.isArray(item.dataLabel) &&
      item.dataLabel.length > 0
    ) {
      item.dataLabel = [item.dataLabel];
    }
    if (item.type === 'table' && item.cols.length > 0) {
      item.cols.forEach((ele) => {
        this.childArr.push({
          value: ele.fieldCode,
          label: ele.fieldName
        });
      });
    }

    this.itemAttribute = item;
    console.log(item.label, item);

    this.stopBubble('sourceDataType');
  }
  stopBubble(e) {
    // 如果提供了事件对象，则这是一个非IE浏览器
    if (e && e.stopPropagation) {
      e.stopPropagation();
    }
    // 因此它支持W3C的stopPropagation()方法
    // e.stopPropagation();
    // 否则，我们需要使用IE的方式来取消事件冒泡
    else {
      const a: any = window.event;
      if (a !== null) a.cancelBubble = true;
    }
  }
  // 表单数据查询
  queryFormData() {
    const customForm = cloneDeep(this.globalFormAttribute);
    if (customForm) {
      customForm.formList.forEach((item) => {
        if (!Object.prototype.hasOwnProperty.call(item, 'dataQueryList')) {
          item.dataQueryList = [];
        }
        if (!Object.prototype.hasOwnProperty.call(item, 'addSetList')) {
          item.addSetList = [];
        }
        if (!Object.prototype.hasOwnProperty.call(item, 'bindValue')) {
          item.bindValue = {};
        }
        // 临时代码，兼顾以前配置
        if (item.type === 'table' && !item.loadingPrimKey) {
          item.refreshLoading = false;
          item.loadingPrimKey = [];
        }
      });
      this.formAttribute = customForm;
      if (
        !Object.prototype.hasOwnProperty.call(this.formAttribute, 'labelSuffix')
      ) {
        this.formAttribute.labelSuffix = ':';
      }
      if (
        !Object.prototype.hasOwnProperty.call(this.formAttribute, 'labelWidth')
      ) {
        this.formAttribute.labelWidth = '120';
      }
      if (
        !Object.prototype.hasOwnProperty.call(
          this.formAttribute,
          'labelPosition'
        )
      ) {
        this.formAttribute.labelPosition = 'right';
      }
    }
  }
  getDataSet() {
    dataViewFields({}).then(({ data, error }) => {
      if (data) {
        this.dataSet = data;
        this.viewDataSet = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  getDataPushSet() {
    dataViewFields({ type: '1' }).then(({ data, error }) => {
      if (data) {
        this.dataSet = data;
        // this.viewDataSet = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  created() {
    this.queryFormData();
    this.getDataSet();
  }
}
